import React, { Component } from "react";
import { Container, Row, Col } from "react-bootstrap";
import "./SignIn/SignIn.css";
import "./pages.css";
import NavBar from "./navbar/NavBar";
import planet from './technology.png';

class Home extends Component {
  constructor(props) {  
    super();
    this.state = {};
  }

changeRoute = (newRoute) => {
  this.props.changeRoute(newRoute);
}
loadUser = (data) =>{
  this.props.loadUser(data);
}

  render() {
    return (
      <div>
        <NavBar changeRoute={this.changeRoute} />

        <div className="tc">
          <br />
          <br />
          <br />
          <h1 className="tc white i f1 font">Memory Games!</h1>
          <h1 className="tc white i f2 font">An Information Technology P.A.T. 2020</h1>
          <h1 className="tc white i f3 font">Done by Jonah Kollenberg   </h1>

          <br />
          <br />
          

          <Container>
            <Row>
           
              <Col className="tc f3 solidBR">
                  What exactly have I built?
               <hr />
                <div className="tl f3 i">
                  Memory Games are a set of four different memory games, all supposed to
                  help strengthen different parts of ones memory. There is a word based 
                  game, a math based game, a postion based game and a pattern based game.
                  As you progress through the different levels of each game, each one gets 
                  slightly more difficult. Also, you can compete with others through my leaderboard

                  <br/>               
                </div>
              </Col>
              <Col>
                
                <img src={planet} alt="yes" />
              </Col>

      
                      </Row>

              <br/>
              <br/>
              <br/>
              
              <br/>
              
          </Container>
          <br/>
          <br/>
        </div>
        </div>
    );
  }
}

export default Home;
