import React, { Component } from "react";
import { Container, Row, Col } from "react-bootstrap";
import "./SignIn/SignIn.css";
import "./pages.css";
import NavBar from "./navbar/NavBar";
import sevens from './sevens.PNG';
import patterns from './patterns.PNG';
import flippingTiles from './flippingTiles.PNG';
import remerber from './remerber.PNG';
import a from './a.PNG';
import b from './b.PNG';
import c from './c.PNG';
import d from './d.PNG';
import e from './e.PNG';
import f from './f.PNG';

class Home extends Component {
  constructor(props) {
    super();
    this.state = {};
  }

changeRoute = (newRoute) => {
  this.props.changeRoute(newRoute);
}

  render() {
    return (
      <div>
        <NavBar changeRoute={this.changeRoute} />

     
        <div className="tc">
          <br />
          <br />
          <br />
          <h1 className="tc white i f1 font">An Overview </h1>
        
          <br />
          <br />
          <Container>
            <Row>
           
              <Col className="tc f3 solidBR">
                  Game 1 - Sevens
               <hr />
                <div className="tl f3 i">
                  This is a basic maths game where the aim is to count as high as possible.
      The computer will show a restriction and the first number.
          You will have to enter the next number which is the lowest number larger
  then the one given by computer which is not a multiple of the restriction
  and does not contain the restriction. For examlpe,
  If restriction is 3. Computer will display 1. You have to enter 2. 
  4 will then be displayed, as 3 is a multiple of the restriction. 
  You will enter 5. Then 7 as 6 is a mutiple. 13 also would can never be
entered as it contains 3. 

                  <br/>               
                </div>
              </Col>
              <Col>
                
                <img src={sevens} alt="yes" />
              
              </Col>

                </Row>
                  <br/>
                  <br/>
                  <br/>
                <Row>

                <Col>
                
               <img src={patterns} alt="yes" />
              
              </Col>
           
              <Col className="tc f3 solidBR">
                  Game 2 - Patterns
               <hr />
                <div className="tl f3 i">
                  When you are ready, you are to push the ready button. 
One at a time, specfic tiles will light up in a pattern. After they
are finished lighting up, you are required to click the tiles, in the
order in which the lighted up. The amount of tiles in each pattern is 
determined by the amount of patters you have gotten right. If you enter 
a wrong sequence or in the correct order, you lose.  

                  <br/>               
                </div>
              </Col>
              

                </Row>

              <br/>
              <br/>
              <br/>
              
              <br/>
                  <Row>
           
              <Col className="tc f3 solidBR">
                  Game 3 - Flipping Tiles
               <hr />
                <div className="tl f3 i">
                This is just a version of the popular memory game. 
When you are ready, click the ready button. 
Then when you click a tile, it will reveal an image.
There are two of each image within the 16 tiles. 
After you pick two if the match, you will get a point
and they are removed from the board. If they do not match, 
you lose a life and both are hidden again. If you lose too 
many lives you lose the game. The amount of lives is determined
by the amount of times you have won this game. 

                  <br/>               
                </div>
              </Col>
              <Col>
                
               <img src={flippingTiles} alt="yes" />
              
              </Col>

                </Row>

              <br/>
              <br/>
              <br/>
              
              <br/>
                  <Row>
           
           <Col>
                
                <img src={remerber} alt="yes" />
              
               </Col>

              <Col className="tc f3 solidBR">
                  Game 4 - Remember the Word
               <hr />
                <div className="tl f3 i">
                This game is a different type of memory game. 
You will be shown a list of words and required to
remember the list and enter them back, one at a time. 
Spelling is aslo important. Also, you can chose between
four languages to make sure you have an edge. The choices are English, 
Afrikaanse, French and Italian. 

                  <br/>               
                </div>
              </Col>
              

                </Row>
                <br/>
                <br/>
                <h1 className="white">Those are just the Games! There is more!</h1>
                 <br/>
                 <br/>
            
           <Row>
           
           <Col>
              <img src={a} alt="yes" />
            </Col>

            <Col className="tc f3 solidBR">
              <img src={b} alt="yes" />
            </Col>
            </Row>

              <br/>
              <br/>
              <br/>
              
              <br/>
              <Row>
           
           <Col>
              <img src={c} alt="yes" />
            </Col>

            <Col className="tc f3 solidBR">
              <img src={d} alt="yes" />
            </Col>
            </Row>

              <br/>
              <br/>
              <br/>
              
              <br/>

              <Row>
           
           <Col>
              <img src={e} alt="yes" />
            </Col>

            <Col className="tc f3 solidBR">
              <img src={f} alt="yes" />
            </Col>
            </Row>

              <br/>
              <br/>
              <br/>
              
              <br/>

              
          </Container>
          <br/>
          <br/>
        </div>
        </div>
    );
  }
}

export default Home;
