import React, { Component } from "react";
import "./App.css";
import "bootstrap/dist/css/bootstrap.css";
import Particles from "react-particles-js";


import Python from './components/python';
import Present from './components/present';
import Calc from './components/Calc';


let cur; 
 
class App extends Component {
  constructor(props){
    super(props);  
    this.state = {  
   
    route: 'home'
  };
  }

  backgroundOptions = {
    particles: {
      number: {
        value: 200,
        density: {
          enable: true,
          value_area: 800
        }
      }
    }
  };

changeRoute = (newer) =>{
  console.log("recieced : " + newer );

  this.setState({'route' : newer});
}




  render() {
    let { route } = this.state;
 

if(route === 'Calc'){ 
   
         cur =  <Calc changeRoute={this.changeRoute}/>
    }

    if(route === 'home'){ 
    		cur = <Python changeRoute={this.changeRoute}/>
    }
    if(route === 'present'){
    	cur = <Present changeRoute={this.changeRoute 	}/>
    }



    if(route === "wa"){ 
      cur = <wa changeRoute={this.changeRoute}
      />;
  }





    return (
      <div>
      
        <Particles className="particles" params={this.backgroundOptions} />
        {cur}

      </div>
    );
  }
}

export default App; 